<template>

  <div id="app">
   <Header id="el-header"></Header>
    <router-view/>
    <Footer></Footer>
  </div>
</template>


<script>

import Header from './components/Header.vue'
import Footer from './components/Footer.vue'

export default {

  components: {
    Header,
    Footer
  },
  mounted () {
    window.addEventListener('scroll', this.windowScroll)
  },
  methods: {
    windowScroll () {
       // 滚动条距离页面顶部的距离
       // 以下写法原生兼容
       let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
       if(scrollTop>50){
          document.getElementById("logoimg").style.width="10rem";
          document.getElementById("headerRow").style.padding="5px 0";

          document.getElementById("el-header").style.position="fixed";
       }else if(scrollTop<200){
         document.getElementById("logoimg").style.width="14.5rem";
         document.getElementById("headerRow").style.padding="20px 0";
         document.getElementById("el-header").style.position="relative";
       }

    }
  }
}
</script>

